import { Component, OnInit } from '@angular/core';
import { HourService } from '../../shared/services/hour.service';

@Component({
  selector: 'app-hours',
  templateUrl: './hours.component.html',
  styleUrls: ['./hours.component.css']
})
export class HoursComponent implements OnInit {
  title = 'Hours';
  rows = [];
  columns = [
    { prop: 'comments', name: 'Comments' },
    { name: 'Hours' }
  ];

  OnDelete(id) {
    this.hourService.deleteHour(id).subscribe(res => {
      this.GetHours();
    });
  }

  GetHours() {
    const studentsObservable = this.hourService.getHours();
    studentsObservable.subscribe((Data: any) => {
      this.rows = Data.data;
    });
  }
  public constructor(private hourService: HourService) {
    this.GetHours();
  }

  public ngOnInit(): void {
  }
}
